
import { defineComponent } from 'vue';

export default defineComponent({
    computed: {
        // User data from API
        user() {
            return this.$store.getters.getUser
        },
    },
    methods: {
        logOut() {
            this.$store.commit('setSignInAnswer', {status: 'Log out'})
            this.$emit('onLogOut')
        }
    }
})
